import { pathOr, keys, map, equals, length, __, join, replace } from 'ramda'
import { mapIndexed, hasPath } from '@/utils/webHelper'
import { SnippetProps, Snippets } from '@/constants/types'
import { Option } from '@/components/snippet-template-selection-modal'

const { compose } = require('ramda')

export function parseReport(list: Snippets) {
  let output = `
${compose(
  join(''),
  map((key: string) => {
    if (compose(equals(__, 0), length, pathOr([], [key]))(list)) {
      return `
${key}
      `
    }

    let questions = `
${compose(
  join('\n'),
  mapIndexed(({ setting, ...s }: SnippetProps, idx: number) => {
    if (s.inline) {
      const div = document.createElement('div')
      div.innerHTML = compose(replace(/\r?\n|\r/g, ''), pathOr('', ['html']))(s)
      return ` ${idx + 1}. ${div.textContent || div.innerText || ''}\n`
    }
    if (hasPath(['options'])(setting)) {
      return compose(
        join('\n'),
        map((o: Option) => ` ・${o.text}`),
        pathOr([], ['options'])
      )(setting)
    }
    return ` ${idx + 1}. ${setting.text || '未輸入題目'}`
  }),
  pathOr([], [key])
)(list)}
      `

    return `
${key}
${questions}
      `
  }),
  keys
)(list)}
    `

  return output
}
